.font-group {
  display: flex;
  justify-content: center;
  align-items: center; }
  .font-group :not(:last-child) {
    margin-right: 2rem; }

p {
  letter-spacing: 0.02rem; }

.layout {
  min-height: 100vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column; }
  .layout .scores {
    background: rgba(255, 255, 255, 0.8);
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 1rem 4rem; }
    .layout .scores .first {
      flex: 0 1 20%; }
      .layout .scores .first.t {
        text-align: center; }
    .layout .scores .middle {
      flex: 0 1 60%;
      text-align: center; }
  .layout .head {
    padding: 3rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%; }

table {
  width: 100%;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.5);
  border: 1px solid #253840;
  border-collapse: collapse; }

th {
  border: 1px solid #e0e0e0;
  padding: 10px;
  text-align: center;
  height: 10rem;
  color: #253840;
  font-family: "Monoton", cursive;
  text-transform: uppercase;
  font-size: 5rem;
  cursor: pointer;
  width: calc(100% / 3); }

tr {
  background: #f9f9f9; }

tr:nth-child(odd) {
  background: #fff; }

.game {
  width: 60%;
  margin: 4rem auto; }

.container {
  position: fixed;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  visibility: hidden;
  z-index: -2;
  display: flex;
  align-items: flex-start;
  justify-content: center; }
  .container.cont2 {
    display: flex;
    align-items: center;
    justify-content: center; }
  .container__model {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 75%;
    flex-direction: column;
    background: #fff;
    border-radius: 4px; }
  .container__settings {
    width: 50rem;
    background: #fff;
    border-radius: 4px; }
  .container .submit {
    padding: 3rem;
    text-align: center; }
